import { ButtonProps } from "@/Button"
import { CardProps } from "@/Card"
import { TypographyProps } from "@/Typography"
import { CardActionsProps } from "@/CardActions"
import { CardBodyProps } from "@/CardBody"
import { CardContentProps } from "@/CardContent"
import { CardHeaderProps } from "@/CardHeader"
import { CardMediaProps } from "@/CardMedia"
import { FormGroupProps } from "@/FormGroup"
import { GalleryProps } from "@/Gallery"
import { HelperTextProps } from "@/HelperText"
import { ImageDialogProps } from "@/ImageDialog"
import { LabelProps } from "@/Label"
import { ListProps } from "@/List"
import { ListItemProps } from "@/ListItem"
import { TextAreaProps } from "@/TextArea"
import { TextFieldProps } from "@/TextField"
import { CSSRuleObject } from "tailwindcss/types/config"
import plugin from "tailwindcss/plugin"
import { CSSProperties } from "react"

interface TypestryComponents {
  TuiButton: ButtonProps
  TuiCard: CardProps
  TuiCardActions: CardActionsProps
  TuiCardBody: CardBodyProps
  TuiCardContent: CardContentProps
  TuiCardHeader: CardHeaderProps
  TuiCardMedia: CardMediaProps
  TuiFormGroup: FormGroupProps
  TuiGallery: GalleryProps
  TuiHelperText: HelperTextProps
  TuiImageDialog: ImageDialogProps
  TuiLabel: LabelProps
  TuiList: ListProps
  TuiListItem: ListItemProps
  TuiTextArea: TextAreaProps
  TuiTextField: TextFieldProps
  TuiTypography: TypographyProps
}

export type TypestryClasses = `.${keyof TypestryComponents}`

interface TypestryConfig {
  overrides?: Partial<Record<TypestryClasses, CSSProperties>>
}

export function typestry(config?: TypestryConfig) {
  const modifiedOverrides = Object.entries(config?.overrides ?? {}).reduce(
    (acc, [key, value]) => {
      const modifiedProps = Object.entries(value).reduce(
        (propsAcc, [propKey, propValue]) => {
          propsAcc[propKey] = `${propValue} !important`
          return propsAcc
        },
        {} as Record<string, string>,
      )

      Object.assign(acc, { [key]: modifiedProps })

      return acc
    },
    {} as Record<TypestryClasses, Record<string, string>>,
  )

  return plugin(
    ({ addComponents }) => {
      addComponents(modifiedOverrides as CSSRuleObject)
    },
    {
      theme: {
        darkMode: "class",
        fontFamily: {
          serif: ["NotoSerif-Variable"],
          body: ["Koulen"],
          sans: ["Finlandica-Variable"],
        },
        extend: {
          colors: {
            primary: {
              50: "#f5f3ff",
              100: "#edeafd",
              200: "#ddd7fd",
              300: "#c3b7fb",
              400: "#a78ff6",
              500: "#8a61f1",
              600: "#7a40e7",
              700: "#753dd6",
              800: "#5926b1",
              900: "#4a2191",
              950: "#2d1362",
            },
          },
        },
      },
    },
  )
}
